  methods: {
    footerMethod({ columns, data }) {
      return [
        columns.map((column, columnIndex) => {
          if (columnIndex === 1) {
            return "合计";
          }
          if (["goods_num", "goods_price","no_revenue_price","revenue_price"].includes(column.property)) {
            var num = this.$utils.sum(data, column.property);
            if (column.property === "goods_num") {
              return this.$publicFution.priceFormatNone(num, 0)
            } else {
              return this.$publicFution.priceFormatNone(num)
            }
          }
          return null;
        })
        ,
        columns.map((column, columnIndex) => {
          if (columnIndex === 1) {
            return "总计";
          }
          if (column.property === 'goods_num' && this.footerData.total_goods_num !== undefined) {
            return this.footerData.total_goods_num
          }
          if (column.property === 'goods_price' && this.footerData.total_goods_price !== undefined) {
            return this.$publicFution.priceFormatNone(this.footerData.total_goods_price)
          }
          if (column.property === 'no_revenue_price' && this.footerData.sub_no_revenue_price !== undefined) {
            return this.$publicFution.priceFormatNone(this.footerData.sub_no_revenue_price)
          }
          if (column.property === 'revenue_price' && this.footerData.sub_revenue_price !== undefined) {
            return this.$publicFution.priceFormatNone(this.footerData.sub_revenue_price)
          }
          return null;
        })]
    },
    getTableData(obj) {
      if (obj.data.code === 200) {
        this.footerData = obj.data.data.desc
      }
    }
  }
