<template>
  <div class="params">
    <div id="leixin">
      <!-- <h3 class="title_container">采购退货明细表</h3> -->
      <div class="search_container">
        <el-form :inline="true" :model="params" class="demo-form-inline">
          <el-form-item label="采购组织" size="mini">
            <el-select clearable v-model="params.company_id" placeholder="请选择" @focus="companyListInvok" @change="companyChange" filterable>
              <el-option v-for="item in companyList" :key="item.company_id" :label="item.company_name" :value="item.company_id">
                <span :class="item.type==1 ? 'optionStyle': '' ">{{ item.company_name }}</span>
              </el-option>
            </el-select>
          </el-form-item>

          <el-form-item label="业务日期" size="mini">
            <el-date-picker v-model="timeValue" type="daterange" range-separator="-" value-format="yyyy-MM-dd" start-placeholder="开始日期" end-placeholder="结束日期">
            </el-date-picker>
          </el-form-item>

          <el-form-item label="单据号" size="mini">
            <el-input v-model="params.refund_sn" placeholder="请输入" @keyup.enter.native='onSearch' clearable></el-input>
          </el-form-item>

          <el-form-item label="供应商(厂家)" size="mini">
            <el-select clearable v-model="params.supplier_id" maxlength="100" @change="supplierChangesave" :filter-method="supplierListInvok" @focus="supplierListInvok" filterable placeholder="请选择">
              <el-option v-for="item in supplierList" :label="item.supplier_name" :value="item.supplier_id" :key="item.supplier_id">
              </el-option>
            </el-select>
          </el-form-item>

          <el-form-item label="来源单号" size="mini">
            <el-input v-model="params.instorage_sn" placeholder="请输入" @keyup.enter.native='onSearch' clearable></el-input>
          </el-form-item>

          <el-form-item label="仓库名称" size="mini">
            <el-select clearable v-model="params.warehouse_id" placeholder="请选择" @focus="warehouseInvok" @change="warehouseChange">
              <el-option :label="item.warehouse_name" :value="item.warehouse_id" :key="item.warehouse_id" v-for="item in warehouseList"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="货位名称" size="mini">
            <el-select clearable class="el_slect" v-model="params.allocation_id" placeholder="请选择" @change="allocationChange" size="mini" @focus="InventoryInvok">
              <el-option :label="item.allocation_name" :value="item.allocation_id" :key="item.allocation_id" v-for="item in allocationInventoryList"></el-option>
            </el-select>
          </el-form-item>

          <el-form-item label="商品编号" size="mini">
            <el-input v-model="params.goods_id" placeholder="请输入" maxLength="7" @keyup.enter.native='onSearch' clearable></el-input>
          </el-form-item>

          <el-form-item label="商品名称" size="mini">
            <el-input v-model="params.goods_name" placeholder="请输入" @keyup.enter.native='onSearch' clearable></el-input>
          </el-form-item>

          <el-form-item label="退货原因" size="mini">
            <el-select clearable v-model="params.reason_id" @focus="getReasonInvok" @change="getReasonChange">
              <el-option v-for="item in reasonList" :label="item.reason_name" :value="item.reason_id" :key="item.reason_id">
              </el-option>
            </el-select>
          </el-form-item>

          <el-form-item label="部门" size="mini">
            <el-select clearable v-model="params.department_id" placeholder="请选择" filterable @focus="DepartmentListInvok" @change="departmentChange">
              <el-option :label="item.department_name" :value="item.department_id" :key="item.department_id" v-for="item in departmentList"></el-option>
            </el-select>
          </el-form-item>

          <el-form-item label="采购员" size="mini">
            {{(function(){if(params.business_id == 0)params.business_id = null})()}}
            <el-select clearable v-model="params.business_id" @focus="EmployeeListInvok" @change="buyerChange">
              <el-option :label="item.employee_name" :value="item.employee_id" :key="item.employee_id" v-for="item in employeeList"></el-option>
            </el-select>
          </el-form-item>

          <el-form-item label="审核状态" size="mini">
            <el-select clearable v-model="params.auditor_status" placeholder="请选择">
              <el-option label="全部" :value="0">全部</el-option>
              <el-option label="未审核" :value="1">未审核</el-option>
              <el-option label="已审核" :value="2">已审核</el-option>
              <el-option label="作废" :value="3">作废</el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="审核日期" size="mini">
            <el-date-picker v-model="timeValue1" type="daterange" range-separator="-" value-format="yyyy-MM-dd" start-placeholder="开始日期" end-placeholder="结束日期" clearable>
            </el-date-picker>
          </el-form-item>
          <el-form-item label="工厂代码" size="mini">
            <el-input v-model="params.factory_code" placeholder="请输入"></el-input>
          </el-form-item>
          <el-form-item size="mini" class="search-style search">
            <el-button @click="onSearch" size="mini" type="primary">查询</el-button>
            <el-button plain @click="onReset" size="mini">重置</el-button>
          </el-form-item>
        </el-form>
      </div>
      <div class="add_delet">
        <el-dropdown placement="bottom-start">
          <span split-button style="cursor:pointer;color:#4e90e2">
            <el-button plain icon="iconfont icon-daochu10" size="mini">导出</el-button>
          </span>
          <el-dropdown-menu slot="dropdown" style="margin-top:10px;">
            <el-dropdown-item @click.native="exportOrder(1)">导出Excel文件</el-dropdown-item>
            <el-dropdown-item @click.native="exportOrder(2)">导出CSV逗号分隔</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </div>
      <div class="table_container">
        <xTable :ajax='{ url: "getRefundGoodsList", params: params }' ref='xTable' @getData='getTableData' :footerMethod='footerMethod' :tableColumn='tableColumn' :xData='tableData' />
      </div>
    </div>
    <el-row type="flex" class="row-bg">
      <el-col :span="12">
        <div>生成时间：{{addTIME}}</div>
      </el-col>
      <el-col :span="12">
        <div class="textAr">制表人：{{realname}}</div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import { opthionList } from "common/utils/public_function/commonUtils";
import { mapActions, mapGetters } from "vuex";
import { TableHead } from "./refundgoods.js";
export default {
  name: "purchaseRefundgoods",
  mixins: [TableHead],
  data() {
    return {
      authority: {},
      timeValue1: [],
      defaultArea: [0], //默认的区域id
      addTIME: this.$publicFution.formatData(new Date()),
      realname: localStorage.realname,
      outerVisible: false,
      searchParams: {},
      loading: true,
      timeValue: [],
      is_shop: 0
    };
  },
  computed: {
    ...mapGetters(["refundgoodsGet"])
  },
  methods: {
    ...mapActions(["refundgoodsIndexAct"]),
    supplierChangesave(val) {
      if (val) {
        this.params.supplier_name = this.supplierList.filter(
          obj => obj.supplier_id === val
        )[0].supplier_name;
      }
    },
    buyerChange(val) {
      if (val) {
        this.params.buyer_name = this.employeeList.filter(
          obj => obj.employee_id === val
        )[0].employee_name;
      }
    },
    warehouseChange(val) {
      if (val) {
        this.params.warehouse_name = this.warehouseList.filter(
          obj => obj.warehouse_id === val
        )[0].warehouse_name;
      }
    },
    allocationChange(val) {
      if (val) {
        this.params.allocation_name = this.allocationInventoryList.filter(
          obj => obj.allocation_id == val
        )[0].allocation_name;
      }
    },
    getReasonChange(val) {
      if (val) {
        this.params.reason_name = this.reasonList.filter(
          obj => obj.reason_id == val
        )[0].reason_name;
      }
    },
    departmentChange(val) {
      if (val) {
        this.params.department_name = this.departmentList.filter(
          obj => obj.department_id === val
        )[0].department_name;
      }
      this.$set(this.params, "business_id", "");
    },
    exportOrder(scheduleType) {
      // 导出库存收发明细表导出URL
      let s = Object.assign(this.params, {
        export_type: scheduleType
      });
      this.$axMethod.exportRefundGoodsListUrl(this.params).then(response => {
        if (response.data.code == 200) {
          window.location.href = response.data.data.url;
        }
      });
    },
    companyChange(val) {
      if (val != "") {
        this.params.company_name = this.companyList.filter(
          obj => obj.company_id === val
        )[0].company_name;
      }
      this.$set(this.params, "supplier_id", "");
      this.$set(this.params, "business_id", "");
      this.$set(this.params, "department_id", "");
      this.$set(this.params, "allocation_id", "");
      this.$set(this.params, "warehouse_id", "");
    },
    onReset() {
      /** 重置时 如需保存默认参数，则： */
      this.defaultArea = [0];
      this.$set(this, "params", {
        auditor_status: 0
      });
      this.timeValue = [];
      this.timeValue1 = [];
      this.onSearch();
    },
    onSearch() {
      //查询
      if (this.timeValue) {
        this.params.start_time = this.timeValue[0];
        this.params.end_time = this.timeValue[1];
      } else {
        this.params.start_time = this.params.end_time = "";
      }
      if (this.timeValue1) {
        this.params.start_auditor_time = this.timeValue1[0];
        this.params.end_auditor_time = this.timeValue1[1];
      } else {
        this.params.start_auditor_time = this.params.end_auditor_time = "";
      }
      this.$refs.xTable._getData(1);
    }
  },
  mounted() {
    this.authority = JSON.parse(localStorage.getItem("authority"));
    if (this.refundgoodsGet && this.refundgoodsGet.params) {
      this.$set(this, "params", this.refundgoodsGet.params);
      if (this.refundgoodsGet.params.start_time) {
        const { start_time, end_time } = this.refundgoodsGet.params;
        this.timeValue = this.timeValue.concat([start_time, end_time]);
      }
      Object.assign(this.refundgoodsGet.params, {
        employee_id: this.refundgoodsGet.params.business_id,
        employee_name: this.refundgoodsGet.params.buyer_name
      });
      opthionList(this, this.refundgoodsGet.params);
    } else {
      this.$set(this, "params", {
        auditor_status: 0
      });
    }
    this.onSearch();
  }
};
</script>
